import React from 'react';
import { observer } from 'mobx-react';
import { Polygon, Map, InfoWindow, GoogleApiWrapper} from 'google-maps-react';

import CustomMarker from './CustomMarker';

const KEY = 'AIzaSyAZk1OQVp3sApAt7PHetwWPGMrqrgnx2Bk';

class MapComponent extends React.Component {
    // TODO: change this to state or ui store
    static defaultProps = {
        center: {
          // kind off center of western europe
          lat: 46.2022,
          lng: 1.264
        },
        zoom: 5
    };

    // temporary way to hide some marker flickering
    // shouldComponentUpdate(nextProps, nextState) {
    //     if (
    //         nextProps.markerData.length !== this.props.markerData.length
    //     ) {
    //         return true
    //     }

    //     return false;
    // }

    render() {
        const { google, onClick, center, zoom, onMapClick, connectedCities } = this.props;
        const { showingInfoWindow, activeMarker, selectedPlace, markerData, likedPoints } = this.props;

        const lineCoords = [];

        // if (connectedCities.length >= 2) {
        //     connectedCities.forEach((city) => {
        //         lineCoords.push({ lat: city.lat, lng: city.lng });
        //     });
        // }

        return (
            <Map
                google={google}
                initialCenter={center}
                zoom={zoom}
                onClick={onMapClick}
                containerStyle={styles.container}
            >
                {
                    markerData.map((point, i) => {
                        return (
                            <CustomMarker 
                                {...this.props}
                                point={point}
                                key={i}
                                onClick={onClick}
                                google={google}
                                likedPoints={likedPoints}
                            />
                        )
                    })
                }   

                {/* <InfoWindow marker={activeMarker} visible={showingInfoWindow}>
                    <div>
                        <h1>{selectedPlace.name}</h1>
                    </div>
                </InfoWindow> */}

                <Polygon
                    paths={lineCoords}
                    strokeColor="#0000FF"
                    strokeOpacity={0.8}
                    strokeWeight={2}
                    fillColor="#0000FF"
                    fillOpacity={0.35} 
                />
            </Map>
        )
    }
}

const styles = {
    container: {
        width: '100%', 
        height: 'calc(100% - 90px)'
    }
}

export default GoogleApiWrapper({
    apiKey: (KEY)
})(observer(MapComponent))
