import React from 'react';
import { observer } from "mobx-react"

import UIStore from '../store/store';

import Sidebar from '../components/Sidebar';
import MapComponent from '../components/Map';
import Header from '../components/Header';

import '../App.css';

import { cities, pointOfInterests } from '../data/data.js';

@observer
class App extends React.Component {
  state = {
    showingInfoWindow: false,
    activeMarker: {},
    selectedPlace: {},
  }

  onClick(props, marker) {
    const { city } = props;

    // this.setState({
    //   selectedPlace: city,
    //   activeMarker: marker,
    //   showingInfoWindow: true
    // });

    UIStore.selectedCity = city;

    // if sidebar open and new city clicked, return
    if (UIStore.showSidebar && city !== UIStore.selectedCity) {
        return;
    }

    UIStore.showSidebar = !UIStore.showSidebar;
  }

  mapClick() {
    UIStore.showSidebar = false;

    // this.setState({
    //   selectedPlace: {},
    //   activeMarker: {},
    //   showingInfoWindow: false
    // });
  }

  onCitySelected(e, select) {
    const cityData = cities.find(city => city.name === e.target.value);

    // TODO: fix this, should be able to do this better
    if (cityData) {
      const connectedCities = UIStore.connectedCities;
      connectedCities[select - 1] = cityData;
      UIStore.connectedCities = connectedCities;
    }
  }

  clearCities() {
    // TODO: need to clear selects
    UIStore.connectedCities = [];
  }

  onTypeSelected(e) {
    UIStore.selectedType = e.target.value;
  }

  render() {
    const { showingInfoWindow, activeMarker, selectedPlace } = this.state;

    const { connectedCities, showSidebar, selectedCity, selectedType } = UIStore;
    const { markerData } = UIStore.getMarkerData();

    return (
      <div style={styles.container}>
        <Header 
          onCitySelected={(city, select) => this.onCitySelected(city, select)}
          onTypeSelected={(e) => this.onTypeSelected(e)}
          clearCities={() => this.clearCities()}
          connectedCities={connectedCities}
        />

        <div style={{ display: 'flex', flex: 1 }}>
          <MapComponent
            markerData={markerData}
            onClick={(props, marker, e) => this.onClick(props, marker, e)}
            onMapClick={() => this.mapClick()}
            likedPoints={UIStore.likedPoints}

            //connectedCities={connectedCities}
            // showingInfoWindow={showingInfoWindow}
            // activeMarker={activeMarker}
            // selectedPlace={selectedPlace}
          />
          <Sidebar 
            selectedCity={selectedCity}
            showSidebar={showSidebar}
            mapClick={() => this.mapClick()} 
          />
        </div>
      </div>
    );
  }
}

const styles = {
  container: {
    display: 'flex',
    flex: 1,
    flexDirection: 'column'
  }
}

export default App;
