import { observable } from 'mobx';
import { cities, pointOfInterests, bodiesOfWater } from '../data/data.js';

class UIStore {
    @observable showSidebar = false;
    @observable selectedCity = null;
    @observable connectedCities = [];
    @observable selectedType = 'All'

    @observable likedPoints = [];

    set showSidebar(show) {
        this.showSidebar = show;
    }

    // get showSidebar() {
    //     return this.showSidebar;
    // }

    set selectedCity(city) {
        this.selectedCity = city;
    }

    set connectedCities(cities) {
        this.connectedCities = cities;
    }

    set selectedType(type) {
        this.selectedType = type;
    }

    set likedPoints(likedPoints) {
        this.likedPoints = likedPoints;
    }

    getMarkerData() {
        let markerData = [];

        if (this.selectedType === 'City') {
            markerData = [cities];
        } else if (this.selectedType === 'Landmark') {
            markerData = [pointOfInterests];
        } else if (this.selectedType === 'Lakes') {
            markerData = [bodiesOfWater];
        } else {
            markerData = [...pointOfInterests, ...cities, ...bodiesOfWater];
        }

        return { markerData };
    }

    getType() {
        if (!this.selectedCity) {
            return null;
        }

        return this.selectedCity.type;
    }

    isFavorited(point) {
        const favorited = this.likedPoints.find((liked) => liked.id === point.id);
        return favorited;
    }

    // TODO: should break this up into multiple functions (remove, push, push initial)
    updateLikedPoints(selectedCity) {
        //let likedPoints = UIStore.likedPoints;

        let likedPoints = this.likedPoints;

        const pointFound = likedPoints.find((point) => point.id === selectedCity.id);
        if (pointFound) {
            likedPoints = likedPoints.filter((point) => {
                console.log('');
                return point.id !== selectedCity.id
            });
            this.likedPoints = likedPoints;
            localStorage.setItem('likedPoints', JSON.stringify(likedPoints));
            return;
        }

        if (likedPoints.length >= 0) {
            likedPoints.push({ id: selectedCity.id });
            localStorage.setItem('likedPoints', JSON.stringify(likedPoints));
            this.likedPoints = likedPoints;
        } else {
            localStorage.setItem('likedPoints', JSON.stringify([{ id: selectedCity.id }]));
            this.likedPoints = [{ id: selectedCity.id }];
        }
    }
}

export default new UIStore();
